.footer {
    $this: &;
    background: $blue-light;
    padding: 40px 0;
    @media (max-width: $lg) {
        padding: 15px 0;
    }
    &__inner {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        column-gap: 5px;
        row-gap: 20px;
        @media (max-width: $lg) {
            grid-template-columns: repeat(2, 1fr);
        }
    }

    &__items {
        display: flex;
        flex-direction: column;
        &:first-child,
        &:last-child {
            @media (max-width: $lg) {
                grid-column: 1/-1;
            }
        }
        &:last-child {
            @media (max-width: $lg) {
                display: grid;
                grid-template-columns: repeat(3, 1fr);
                #{$this}__name {
                    grid-column: 1/-1;
                }
            }
            @media (max-width: $md) {
                display: grid;
                grid-template-columns: repeat(2, 1fr);
                #{$this}__name {
                    grid-column: 1/-1;
                }
                #{$this}__items-contact:last-child {
                    grid-column: 1/-1;
                    align-items: center;
                    justify-content: center;
                }
            }
        }
    }

    &__logo {
        max-width: 70px;
        height: auto;
    }
    &__name-comp {
        margin-top: 20px;
        font-family: Montserrat;
        font-size: 11px;
        font-style: normal;
        font-weight: 500;
        line-height: 18px;
        color: $white;
        @media (max-width: $lg) {
            display: none;
        }
    }
    &__name {
        font-family: Montserrat;
        font-size: 14px;
        font-style: normal;
        font-weight: 600;
        line-height: 30px;
        color: $white;
        margin-bottom: 7px;
    }

    &__list {
        display: flex;
        flex-direction: column;
    }

    &__link {
        font-family: Montserrat;
        font-size: 12px;
        font-style: normal;
        font-weight: 400;
        line-height: 30px;
        color: #fff;
        transition: color 0.2s ease, text-decoration 0.2s ease;
        &:hover {
            color: $secondary;
            text-decoration: underline;
        }
        &--big {
            font-family: Montserrat;
            font-size: 14px;
            font-style: normal;
            font-weight: 600;
            line-height: 30px;
            color: $white;
            @media (max-width: $lg) {
                font-size: 12px;
                line-height: 30px;
            }
        }
        @media (max-width: $lg) {
            font-size: 12px;
            line-height: 24px;
        }
    }

    &__items-contact {
        display: flex;
        gap: 10px;
        margin-bottom: 5px;
    }

    &__img {
    }

    &__text {
        font-family: Montserrat;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 30px;
        color: #fff;
        @media (max-width: $lg) {
            font-size: 14px;
            line-height: 30px;
        }
    }
}
