.text {
    &__title {
        padding-left: 20px;
        font-family: Montserrat;
        font-style: normal;
        font-weight: 800;
        line-height: 1.3;
        @include text-adaptive(30,18);
        color: $primary;
        position: relative;
        &:after{
            content: '';
            display: block;
            position: absolute;
            top: 50%;
            transform: translate(0,-50%);
            left: 0;
            height: calc(100% * 0.625);
            width: 5px;
            background: $secondary;
            @media (max-width: $md){
                display: none;
            }
        }
    }
}
