.catalog {
    $this: &;
    margin-bottom: 90px;
    @media (max-width: $lg){
        margin-bottom: 40px;
    }
    &__inner {
    }

    &__title {
        margin-bottom: 60px;
        @media (max-width: $lg){
            margin-bottom: 15px;
        }
    }

    &__content {
        display: grid;
        grid-template-columns: 286px 1fr;
        column-gap: 20px;
        @media (max-width: $lg){
            grid-template-columns: 1fr;
        }
    }

    &__menu {
    }

    &__items {
        padding: 14px 0;
        position: relative;
        cursor: pointer;
        overflow: hidden;
        background: #fff;
        &::before {
            display: block;
            position: absolute;
            content: "";
            width: 100%;
            height: 1px;
            top: 0;
            left: 0;
            background: #efefef;
        }
        &:first-child::before {
            display: none;
        }
        &--active {
            #{$this}__dropdown {
                max-height: 200px;
            }

            #{$this}__name {
                color: $secondary;
            }
        }
    }

    &__name {
        color: $primary;
        font-family: Montserrat;
        font-size: 14px;
        font-style: normal;
        font-weight: 700;
        line-height: 22px;
    }

    &__dropdown {
        overflow: hidden;
        max-height: 0px;
        transition: max-height 0.5s ease;
    }

    &__description {
        font-family: Montserrat;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 22px;
        color: $primary;
        margin: 10px 0;
    }

    &__btn {
        @media (max-width: $lg){
            max-width: 250px;
            
        }
    }
    &__catalog {
        overflow: hidden;
        position: relative;
        @media (max-width: $md){
            display: none;
        }
    }

    &__list {
        bottom: 100%;
        left: 0;
        position: absolute;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        column-gap: 20px;
        row-gap: 20px;
        transition: bottom 0.5s ease;
        &--active {
            position: relative;
            bottom: 0;
        }
    }

    &__list-items {
        position: relative;
        &--big {
            grid-row: 1/3;
            &:last-child {
                grid-column: 3/-1;
            }
        }
    }

    &__list-img {
        width: 100%;
    }
    &__list-btn {
        padding: 0 20px;
        position: absolute;
        bottom: 0;
        left: 0;
        width: 150px;
        height: 40px;
        background: #fff;
        display: flex;
        align-items: center;
        justify-content: space-between;
        border: 1px solid #f1f1f1;
    }

    &__list-text {
        font-family: Montserrat;
        font-weight: 700;
        font-size: 14px;
        line-height: 24px;
        color: $primary;
    }

    &__list-icon {
        font-family: Montserrat;
        font-weight: 700;
        font-size: 20px;
        line-height: 24px;
        color: $secondary;
    }
}
