.download {
    $this: &;
    margin-bottom: 20px;
    @media (max-width: $lg){
        margin-bottom: 0;
    }
    &__inner {
        display: grid;
        grid-template-columns: 280px 1fr;
        align-items: flex-start;
        @media (max-width: $lg){
            grid-template-columns: 1fr;
        }
    }

    &__left {
        position: relative;
        margin-top: 15px;
        @media (max-width: $lg){
            display: none;
        }
    }

    &__text {
        white-space: nowrap;
        font-family: Montserrat;
        font-size: 20px;
        font-style: normal;
        font-weight: 800;
        line-height: 24px;
        color: $primary;
    }

    &__left-img {
        position: absolute;
        left: -50px;
        top: -61px;
        z-index: -1;
    }

    &__right {
        height: 150px;
        overflow-y: scroll;
        padding-right: 40px;
        &::-webkit-scrollbar {
            width: 4px;
            background-color: transparent;
        }

        &::-webkit-scrollbar-thumb {
            width: 5px;
            border-radius: 3px;
            background-color: $secondary;
        }

        &::-webkit-scrollbar-track {
            background-color: #e5e5e5;
            width: 1px;
        }
    }

    &__items {
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 50px;
        position: relative;
        &::after {
            position: absolute;
            bottom: 0;
            left: 0;
            height: 1px;
            width: 100%;
            background: #efefef;
            content: "";
            display: block;
        }
    }

    &__name {
        font-family: Montserrat;
        font-size: 14px;
        font-style: normal;
        font-weight: 500;
        line-height: 45px;
        color: #505050;
    }

    &__items-right {
        display: flex;
        align-items: center;
        gap: 20px;
    }

    &__check {
        color: #505050;
        font-family: Montserrat;
        font-size: 14px;
        font-style: normal;
        font-weight: 700;
        line-height: 45px;
        transition: color 0.2s ease;
        &:hover {
            color: $secondary;
        }
        @media (max-width: $lg){
            display: none;
        }
    }

    &__download {
        display: flex;
        align-items: center;
        gap: 10px;

        &:hover {
            #{$this}__download-text {
                color: $secondary;
            }
        }
    }

    &__download-text {
        font-family: Montserrat;
        font-size: 14px;
        font-style: normal;
        font-weight: 700;
        line-height: 45px;
        color: $primary;
        transition: color 0.2s ease;
        @media (max-width: $lg){
            display: none;
        }
    }

    &__download-img {
        min-height: 21px;
        min-width: 21px;
    }
}
