.header {
    $this: &;
    background: $blue-dark;
    padding: 13px 0;
    .container {
        max-width: 1350px;
    }
    &__inner {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__logo {
    }

    &__nav {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 40px;
        @media (max-width: $xl) {
            display: none;
        }
    }
    &__items {
    }

    &__link {
        display: block;
        font-family: Montserrat;
        font-size: 14px;
        font-style: normal;
        font-weight: 500;
        line-height: 17px;
        letter-spacing: 0em;
        text-align: left;
        color: $white;
        position: relative;
        &::after {
            display: block;
            content: "";
            position: absolute;
            height: 1px;
            width: 0%;
            bottom: -5px;
            left: 0;
            background: $white;
            opacity: 0;
            transition: width 0.2s ease, opacity 0.2s ease;
        }
        &:hover {
            &::after {
                opacity: 1;
                width: 100%;
            }
        }
        &--active {
            font-weight: 700;
        }
        &--yellow {
            display: flex;
            align-items: center;
            gap: 9px;
            color: $secondary;
            &::after {
                background: $secondary;
            }
        }
    }

    &__link-text {
    }

    &__link-img {
    }

    &__contact {
        display: flex;
        align-items: center;
        gap: 12px;
    }

    &__left,
    &__right {
        display: flex;
        gap: 10px;
        @media (max-width: $md) {
            display: none;
        }
    }
    &__left {
    }

    &__left-img,
    &__right-img {
        height: 17px;
        width: 17px;
    }
    &__left-text {
        font-family: Montserrat;
        font-size: 14px;
        font-style: normal;
        font-weight: 500;
        line-height: 17px;
        color: $white;
    }

    &__right {
        height: 44px;
        width: 214px;
        justify-content: center;
        align-items: center;
        border: 1px solid $white;
        border-radius: 2px;
    }
    &__right-text {
        font-family: Montserrat;
        font-size: 14px;
        font-style: normal;
        font-weight: 700;
        line-height: 17px;
        color: $white;
    }
}
.burger {
    --burger-width: 30px;
    --burger-height: 20px;
    --burger-line-height: 2px;
    display: none;
    @include burger;
    @media (max-width: $xl) {
        display: block;
    }
    &__menu {
        background: $blue-dark;
        position: fixed;
        z-index: 1001;
        bottom: 100%;
        left: 0;
        width: 100%;
        height: 100%;
        display: none;
        transition: bottom 0.3s ease;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        @media (max-width: $xl) {
            display: flex;
        }
    }
    &__close {
        z-index: 1002;
        right: 12px;
        top: -100%;
        display: none;
        position: fixed;
        color: $secondary;
        font-family: Montserrat;
        font-size: 30px;
        font-style: normal;
        font-weight: 700;
        line-height: 40px;
        transform: rotate(45deg);
    }
    &--active {
        .burger__menu {
            bottom: 0;
        }
        .burger__close {
            top: 14px;
            display: block;
        }
        .header__left {
            position: relative;
            padding-top: 20px;
            &::after {
                width: calc(100vw * 0.9);
                height: 1px;
                content: "";
                display: block;
                background: $primary;
                position: absolute;
                top: 0;
                left: 50%;
                transform: translate(-50%);
            }
        }
        .header__left,
        .header__right {
            margin-bottom: 20px;
            @media (max-width: $md) {
                display: flex;
            }
        }
        .header__items {
            margin-bottom: 20px;
        }
    }
}
