.intro {
    $this: &;
    margin-bottom: 90px;
    @media (max-width: $lg){
        margin-bottom: 40px;
    }
    .container {
    }   

    &__inner {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        align-items: center;
        column-gap: 20px;
        position: relative;
        @media (max-width: $lg){
            grid-template-columns: 1fr;
        }
    }

    &__left {
        @media (max-width: $lg){
            grid-row: 2/3;
        }
    }

    &__content {
    }

    &__title {
        max-width: 561px;
        color: $primary;
        font-family: Montserrat;
        font-style: normal;
        font-weight: 800;
        line-height: 1;
        margin-bottom: 20px;
        @include text-adaptive(36, 24);
        span {
            color: $secondary;
        }
        @media (max-width: $lg){
            max-width: 100%;
        }
    }

    &__description {
        max-width: 485px;
        color: $primary;
        font-family: Montserrat;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 1.5;
        @media (max-width: $lg){
            max-width: 100%;
        }
    }

    &__btn {
        width: 197px;
        margin-top: 21px;
    }

    &__right {
    }
    // slider
    &__slider-nav {
        position: absolute;
        left: 50%;
        transform: translate(-100%);
        bottom: 0;
        display: flex;
        flex-direction: column;
        gap: 10px;
    }

    &__slider-arrows {
        display: flex;
        align-items: center;
        justify-content: center;
        border: 1px solid #EDEDED;
        width: 32px;
        height: 27px;
        cursor: pointer;
        background: $white;
        border-radius: 3px;
        transition: background 0.2s ease;
        &:hover{
            background: $primary;
            #{$this}__slider-arrow{
                path{
                    fill: $white;
                }
            }
        }
        &--right{
            #{$this}__slider-arrow{
                transform: rotate(180deg);
            }
        }
        @media (max-width: $lg){
            display: none;
        }
    }

    &__slider-arrow {
        path{
            transition: fill 0.2s ease;
        }
        
    }

    &__slider {
        @media (max-width: $lg){
            margin-bottom: 40px;
        }
    }

    &__slider-list {
    }

    &__slider-items {
        display: block;
        transition: opacity 0.2s ease;
        width: 100%;
        height: 450px;
        background: url(../img/intro1.png) center center / cover no-repeat;
        transition: background 0.2s ease;
        @media (max-width: $lg){
            height: calc(100vw * 0.3);
        }
    }
    &__slider-img {
        user-select: none;
    }

    &__slider-dots {
        margin-top: 15px;
        height: 1px;
        width: 100%;
        background: #EFEFEF;
        position: relative;
        @media (max-width: $lg){
            display: none;
        }
    }
    &__slider-dot{
        position: absolute;
        top: 0;
        transform: translate(0,-50%);
        left: 0;
        height: 4px;
        width: calc(100% / 3);
        background:$secondary;
        border-radius: 4px;
        transition: left 0.2s ease;
    }
}
