.services {
    $this: &;
    margin-bottom: 90px;
    @media (max-width: $lg){
        margin-bottom: 40px;
    }
    .container {
    }
    &__inner {
    }

    &__title {
        margin-bottom: 50px;
        @media (max-width: $lg){
            margin-bottom: 20px;
        }
    }

    &__list {
        display: flex;
        align-items: center;
        flex-direction: column;
    }

    &__items {
        cursor: pointer;
        display: flex;
        padding: 4px;
        gap: 50px;
        &--active {
            #{$this}__plus {
                transform: rotate(45deg);
            }
        }
    }

    &__left {
        margin-top: 7px;
        width: 42px;
        height: 42px;
    }

    &__img {
        min-width: 42px;
        min-height: 42px;
    }

    &__right {
        position: relative;
        padding: 10px 0;
        &::after {
            display: block;
            position: absolute;
            content: "";
            height: 1px;
            width: 100%;
            background: #e5e5e5;
            bottom: 0;
            left: 0;
        }
    }
    &__top {
        display: flex;
        width: 100%;
        align-items: center;
        justify-content: space-between;
    }
    &__name {
        font-family: Montserrat;
        font-size: 16px;
        font-style: normal;
        font-weight: 700;
        line-height: calc(14px + 30 * (100vw / 1920));
        letter-spacing: 0em;
        text-align: left;
        color: $primary;
        @media (max-width: $lg){
            font-size: 14px;
        }
    }
    &__plus {
        font-family: Montserrat;
        font-style: normal;
        font-weight: 700;
        font-size: 30px;
        line-height: 40px;
        color: $secondary;
        transition: transform 0.3s ease;
        margin-right: 20px;
        @media (max-width: $lg){
            display: none;
        }
    }
    &__accordion {
        overflow: hidden;
        max-height: 0px;
        transition: max-height 0.3s ease;
        padding-right: 140px;
        @media (max-width: $lg){
            padding-right: 0;
        }
    }

    &__text {
        font-family: Montserrat;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 24px;
        color: $primary;
        cursor: text;
        @media (max-width: $lg){
            font-size: 12px;
        }
    }
}
