.new {
    $this: &;
    margin-bottom: 70px;
    @media (max-width: $lg) {
       margin-bottom: 40px;
    }
    .container {
    }
    &__inner {
    }

    &__title {
        margin-bottom: 50px;
    }

    &__list {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        row-gap: 20px;
        column-gap: 20px;
        @media (max-width: $lg) {
            grid-template-columns: repeat(2, 1fr);
        }
    }

    &__items {
        position: relative;
        border: 1px solid #eaeaea;
        transition: box-shadow 0.2s ease;
        &:hover {
            box-shadow: 0px 0px 30px rgba(255, 201, 79, 0.5);
            border-radius: 0px;
            #{$this}__price-text {
                color: $secondary;
            }
            #{$this}__price-img {
                path {
                    fill: $secondary;
                }
            }
        }
    }
    &__day {
        width: 50px;
        height: 31px;
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: 0;
        left: 0;
        background: $secondary;
        @media (max-width: $lg) {
            width: 36px;
            height: 20px;
        }
    }

    &__day-text {
        color: $blue-light;
        font-family: Montserrat;
        font-size: 16px;
        font-style: normal;
        font-weight: 600;
        line-height: 1.5;
        @media (max-width: $lg) {
            font-size: 14px;
            line-height: 24px;
        }
    }
    &__img {
        position: absolute;
        top: 140px;
        left: 50%;
        transform: translate(-50%, -50%);
        @media (max-width: $lg) {
            top: 73px;
            max-height: 100px;
            max-width: 130px;
        }
    }
    &__name,
    &__ul,
    &__price {
        padding: 0 19px;
    }
    &__name {
        margin-top: 280px;
        font-family: Montserrat;
        font-size: 14px;
        font-style: normal;
        font-weight: 700;
        line-height: 22px;
        color: $primary;
        @media (max-width: $lg) {
            margin-top: 146px;
            font-size: 11px;
            line-height: 18px;
        }
    }

    &__ul {
        margin-top: 10px;
    }

    &__li {
        font-family: Montserrat;
        font-size: 14px;
        font-style: normal;
        font-weight: 300;
        line-height: 21px;
        color: $primary;
        position: relative;
        padding-left: 15px;
        &::after {
            position: absolute;
            content: "";
            display: block;
            width: 6px;
            height: 6px;
            border-radius: 50%;
            background: $secondary;
            top: 6px;
            left: 0;
        }
        @media (max-width: $lg) {
            display: none;
        }
    }

    &__price {
        margin-top: 20px;
        display: flex;
        align-items: center;
        gap: 8px;
    }

    &__price-text {
        font-family: Montserrat;
        font-size: 20px;
        font-style: normal;
        font-weight: 700;
        line-height: 21px;
        color: $primary;
        transition: color 0.2s ease;
        @media (max-width: $lg) {
            font-size: 14px;
            line-height: 21px;
        }
    }

    &__price-img {
        path {
            transition: fill 0.2s ease;
        }
    }
    &__bot {
        margin-top: 40px;
        display: flex;
        align-items: center;
        &:hover {
            #{$this}__btn {
                background: darken($blue-light, 10%);
            }
        }
        @media (max-width: $lg) {
            margin-top: 20px;
        }
    }
    &__btn {
        height: 55px;
        width: 100%;
        position: relative;
    }

    &__btn-text {
    }

    &__btn-block {
        width: 55px;
        height: 55px;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__btn-plus {
        font-family: Montserrat;
        font-size: 24px;
        font-style: normal;
        font-weight: 700;
        line-height: 45px;
        color: $primary;
    }
    &__bot-btn {
        width: 214px;
        margin: 0 auto;
        margin-top: 80px;
        margin-bottom: 30px;
        @media (max-width: $lg){
            margin-bottom: 20px;
            margin-top: 20px;
        }
    }
}
