.btn {
    &__yellow {
        border: none;
        height: 44px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: $secondary;
        color: $primary;
        font-family: Montserrat;
        font-size: 14px;
        font-style: normal;
        font-weight: 700;
        line-height: 17px;
        transition: background 0.2s ease;
        &:hover{
            background: darken($secondary,20%);
        }
    }
    &__blue{
        border: none;
        height: 44px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: $blue-light;
        color: $white;
        font-family: Montserrat;
        font-size: 14px;
        font-style: normal;
        font-weight: 700;
        line-height: 17px;
        transition: background 0.2s ease;
        &:hover{
            background: darken($blue-light,10%);
        }
    }
}
