.form {
    margin-top: 70px;
    margin-bottom: 100px;
    @media (max-width: $lg) {
        margin-bottom: 40px;
        margin-top: 40px;
    }
    &__inner {
        display: flex;
        justify-content: center;
        @media (max-width: $lg) {
            flex-direction: column;
            align-items: center;
        }
    }

    &__left {
        max-width: 424px;
        position: relative;
        @media (max-width: $lg){
            max-width: 100%;
        }
    }

    &__title {
        font-family: Montserrat;
        font-size: 24px;
        font-style: normal;
        font-weight: 800;
        line-height: 34px;
        color: $primary;
        @media (max-width: $lg) {
            font-size: 18px;
            line-height: 30px;
            margin-bottom: 30px;
            text-align: center;
        }
    }

    &__img {
        position: absolute;
        left: -90px;
        top: -56px;
        @media (max-width: $lg) {
            display: none;
        }
    }

    &__right {
        max-width: 360px;
        display: flex;
        align-items: flex-end;
        flex-direction: column;
        gap: 20px;
        width: 100%;
        @media (max-width: $lg) {
            align-items: center;
        }
    }

    &__input {
        max-height: 100px;
        overflow-y: hidden;
        min-height: 20px;
        width: 100%;
        border: none;
        background: transparent;
        font-family: Montserrat;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 17px;
        color: $primary;
        position: relative;
        padding-bottom: 6px;
        border-bottom: 2px solid #efefef;
    }

    &__btn {
        width: 214px;
        margin-top: 10px;
        @media (max-width: $lg) {
            align-items: center;
        }
    }
}
